@import url("https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap");

.header {
  margin: 10px 10px 10px 10px;
  display: grid;
  grid-template-columns: 2.25fr 1.50fr 1.05fr;
  background-image: linear-gradient(
    to right,
    rgb(224, 46, 46),
    rgb(224, 46, 46),
    rgb(255, 233, 136)
  );
  padding-top: 30px;
  padding-bottom: 60px;
}

.header:hover {
  /* padding-top: 2em; */
  /* padding-bottom: 2em; */
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.678),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.534),
    0 100px 80px rgba(0, 0, 0, 0.12);

  /* min-height: 100px;
  width: 98vw;
  margin: 10px auto;
  border-radius: 1px; */
}

.title {
  font-family: "Amatic SC", cursive;
  font-size: 60px;
  text-shadow: 7px 7px rgb(255, 180, 243);
}

.slogan {
  font-family: "Amatic SC", cursive;
  font-size: 40px;
  color: #e1f5d0;
}

.account {
  padding-left: 50px;
}

.rose {
  font-size: 5rem;
  padding-top: 30px;
  text-align: center;
  margin-left: -100px;
  height: 15vh;
  line-height: 5vh;
  color: #fcedd8;
  /* background: #d52e3f; */
  font-family: "Niconne", cursive;
  font-weight: 900;
  text-shadow: 5px 5px 0px #eb452b, 10px 10px 0px #efa032, 15px 15px 0px #46b59b,
    20px 20px 0px #017e7f, 25px 25px 0px #052939, 30px 30px 0px #c11a2b,
    35px 35px 0px #c11a2b, 40px 40px 0px #c11a2b, 45px 45px 0px #c11a2b;
}

.td {
  display: table-cell;
  vertical-align: middle;
}

input,
button {
  color: #fff;
  font-family: Nunito;
  padding: 0;
  margin: 0;
  border: 0;

  background-color: transparent;
}

.submit {
  background-color: white;

  position: relative;

  display: block;
  width: 80px;
  height: 66px;
  cursor: pointer;
  border-radius: 4px;
}

#cover {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  width: 550px;
  padding: 35px;
  margin: 0 auto 0 auto;
  background-color: #ff7575;
  border-radius: 20px;
  box-shadow: 0 10px 40px #ff7c7c, 0 0 0 20px #ffffffeb;
  transform: scale(0.6);
}

form {
  height: 96px;
  margin-top: 50px;
}

input {
  border: 2px solid rgba(245, 181, 97, 0.623);
}

input[type="text"] {
  height: 65px;
  font-size: 60px;
  line-height: 1;
  width: 525px;
}

input[type="text"]::placeholder {
  color: #ffffff;
}

span {
  border-bottom: 1px solid #38865b;

  padding-bottom: 0.25em;
  text-decoration: none;
}

span:hover {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg id='squiggle-link' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:ev='http://www.w3.org/2001/xml-events' viewBox='0 0 20 4'%3E%3Cstyle type='text/css'%3E.squiggle{animation:shift .4s linear infinite;}@keyframes shift {from {transform:translateX(0);}to {transform:translateX(-20px);}}%3C/style%3E%3Cpath fill='none' stroke='%23453886' stroke-width='2' class='squiggle' d='M0,3.5 c 5,0,5,-3,10,-3 s 5,3,10,3 c 5,0,5,-3,10,-3 s 5,3,10,3'/%3E%3C/svg%3E");
  background-position: bottom;
  background-repeat: repeat-x;
  background-size: 50%;
  border-bottom: 0;
  padding-bottom: 0.3em;
  text-decoration: none;
}

